.order{color:#292929;font-size:24px;max-width:1440px;margin:0 auto;transform:translate(0, -200px);position:relative;z-index:100}.order__title{color:#fff;font-size:72px;font-style:normal;font-weight:700;margin-bottom:30px}.order__body{border:1px solid #c4c4c4;background:#fff;box-shadow:0px 2px 2px 0px rgba(0,0,0,.25)}.order-card__actions{display:flex;justify-content:space-evenly;padding:40px;background:#f4f3f6}.header-order{border-bottom:1px solid #c4c4c4;padding:60px 80px;display:flex;justify-content:space-between}.header-order__number{color:#3e3c41;font-size:36px;font-weight:700}.header-order__sum{color:#3e3c41;font-size:36px;font-weight:700}.header-order__sum::after{content:" ";color:#928f94;font-weight:300}.header-order__sum span{display:inline-block;margin-right:10px;color:#928f94;font-weight:400}.actions-order{display:flex;flex-direction:column;max-width:175px;text-align:center}.actions-order__image{margin-bottom:11px}.actions-order__text{color:#292929;font-size:18px;text-wrap:balance}.actions-order__text span{font-weight:700}.appeal-order{padding:95px 111px 129px 205px}.appeal-order__title{font-size:36px;font-weight:700;margin-bottom:51px}.appeal-order__text span{margin-top:71px;display:block;font-weight:700}.footer-order{background:rgba(255,168,0,.88);padding:50px 80px;display:flex;justify-content:space-between}.footer-order__btn{cursor:pointer;font-weight:700;text-transform:uppercase;border-radius:5px;border:1px solid #3e3c41;padding:16px 12px;transition:.3s}.footer-order__btn:hover{box-shadow:0px 4px 4px 0px rgba(0,0,0,.25)}.footer-order__btn:active{box-shadow:0px 4px 4px 0px rgba(0,0,0,.25);background:rgba(255,168,0,.88);border-color:rgba(255,168,0,.88);box-shadow:0px 0px 0px 0px rgba(0,0,0,.25)}