.content{background:#f7f5f9;display:grid;grid-template:auto/1fr minmax(auto, 1400px) 1fr}.content__body{padding:90px 0px 190px 0px;grid-column:2/3;display:grid;grid-template:auto/360px 1fr;column-gap:86px;grid-template-areas:"bar filter" "bar tickets" ". tickets" ". tickets"}.content__filters{grid-area:filter}.content__aside{grid-area:bar}.content__tickets{grid-area:tickets;overflow:hidden}.passengers__btn,.choice-of-places-btn{margin-top:70px;justify-self:end;padding:15px 70px;cursor:pointer;color:#fff;font-size:24px;font-weight:700;text-transform:uppercase;border-radius:5px;background:#ffa800;transition:.3s}.passengers__btn:hover,.choice-of-places-btn:hover{box-shadow:0px 4px 4px 0px rgba(0,0,0,.25)}.passengers__btn:active,.choice-of-places-btn:active{outline:1px solid #ffa800;color:#ffa800;background:rgba(0,0,0,0)}.content__passengers{grid-area:filter}.content__bar-details{grid-area:bar}.passengers{display:grid;row-gap:44px}.bar-details{background:#3e3c41}.bar-details__title{padding:35px 10px 30px 10px;text-align:center;color:#e5e5e5;font-size:30px;font-weight:500;text-transform:uppercase}.bar-details__border{height:1px;background:#e5e5e5}.bar-details__there{padding:16px 28px 53px 28px}.bar-details__back{padding:16px 28px 34px 28px}.bar-details__passengers{padding:16px 28px 46px 28px}.bar-details__total{color:#ffa800;font-size:48px;font-weight:700;padding:30px 28px 33px 28px;display:flex;justify-content:flex-end;align-items:flex-end}.bar-details__total::after{content:"";font-weight:300;color:#fff;margin-left:7px}.bar-details__total span{display:inline-block;color:#fff;font-size:30px;font-weight:700;text-transform:uppercase;margin-right:auto}