.content__tickets{display:grid;row-gap:50px;min-width:0}.tickets{display:grid;grid-template:auto/0.4fr 1fr .6fr;border:1px solid #c4c4c4;background:#fff;box-shadow:0px 2px 2px 0px rgba(0,0,0,.3);min-width:0}.tickets__direction{background:#e4e0e9;color:#292929;font-size:16px;font-weight:400;padding:50px 25px 90px 0px;display:flex;flex-direction:column;align-items:center}.tickets__time{padding:55px 10px 80px 10px}.tickets__follow-up-time{display:flex;flex-direction:column;align-items:center;padding-top:6px}.tickets__cost{border-left:1px solid #e5e5e5}.direction-tickets__image{width:86px;height:86px;border:3px solid #fff;border-radius:50%;display:flex;align-items:center;justify-content:center;margin-bottom:17px}.direction-tickets__img{width:44px;height:52px}.direction-tickets__train-number{color:#3e3c41;font-size:24px;font-weight:700;margin-bottom:15px}.direction-tickets__cities{display:flex;flex-direction:column;padding-left:25px}.direction-tickets__cities .city-nn{color:#928f94}.time-tickets__row{display:flex;justify-content:center;column-gap:40px;margin-bottom:40px}.time-tickets__there{display:flex;flex-direction:column}.time-tickets__time{color:#000;font-size:24px;font-weight:700;margin-bottom:3px}.time-tickets__city{color:#292929;font-size:18px}.time-tickets__railway-station{color:#928f94;font-size:16px}.time-tickets__from{display:flex;flex-direction:column}.follow-up-time__time{color:#c4c4c4;font-size:18px;margin-bottom:9px}.follow-up-time__arrow{width:30px;height:20px}.cost-tickets{padding:40px 20px 20px 25px;display:flex;flex-direction:column;height:100%}.cost-tickets__row{display:flex;justify-content:space-between;align-items:center;margin-bottom:12px}.cost-tickets__view{color:#3e3c41;font-size:16px}.cost-tickets__quantity{color:#ffa800;font-size:16px;font-weight:500}.cost-tickets__price{color:#2d2b2f;font-size:24px;font-weight:700;display:flex;align-items:center}.cost-tickets__price:after{content:" ";margin-left:10px;color:#928f94;font-weight:300}.cost-tickets__min{color:#928f94;font-size:16px;font-weight:400;margin-right:10px}.cost-tickets__extras{margin-top:30px;display:flex;justify-content:end;flex:1 1 auto}.cost-tickets__extras img{height:20px;width:92px}.cost-tickets__btn{cursor:pointer;color:#fff;text-align:center;font-size:24px;font-weight:700;border-radius:5px;background:#ffa800;padding:4px 5px 6px 5px;transition:.3s}.cost-tickets__btn:hover{box-shadow:0px 4px 4px 0px rgba(0,0,0,.25)}.cost-tickets__btn:active{color:#ffa800;background:#fff;outline:1px solid #ffa800;box-shadow:none}.cost-tickets__btn-check{cursor:pointer;color:#fff;text-align:center;font-size:24px;font-weight:700;border-radius:5px;background:#fff;padding:4px 5px 6px 5px;transition:.3s;color:#292929;border-radius:5px;border:1px solid #292929}.cost-tickets__btn-check:hover{color:#ffa800;box-shadow:0px 4px 4px 0px rgba(0,0,0,.25)}.cost-tickets__btn-check:active{color:#292929;background:#ffa800;border-color:#ffa800}