.recent-tickets{padding:94px 0px;cursor:pointer}.recent-tickets__title{color:#000;font-size:30px;font-weight:500;text-transform:uppercase;margin-bottom:26px}.recent-tickets__ticket{border:1px solid #c4c4c4;background:#fff;box-shadow:0px 2px 2px 0px rgba(62,60,65,.25);color:#292929;font-size:20px;display:grid;padding:18px;row-gap:24px;column-gap:5px;grid-template:auto/repeat(2, 1fr)}.recent-tickets__ticket+.recent-tickets__ticket{margin-top:17px}.recent-tickets__where,.recent-tickets__from{display:grid}.recent-tickets__where span,.recent-tickets__from span{margin-top:5px;color:#928f94;font-size:16px;font-weight:400;max-width:105px}.recent-tickets__from{grid-column:1/2}.recent-tickets__where{grid-column:2/3;display:grid;justify-items:end}.recent-tickets__where span{text-align:right}.recent-tickets__image{grid-row:2/3;grid-column:1/2;align-self:end}.recent-tickets__price{grid-row:2/3;grid-column:2/3;color:#928f94;text-align:right;font-size:16px}.recent-tickets__price span{color:#ffa800;font-size:36px;font-weight:700}.recent-tickets__price span:after{content:" ";color:#928f94;font-weight:300}